.find__mov {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.mov__list {
    column-count: 4;
    
}

.mov__link {
        display: flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            text-align: center;
            margin-bottom: 30px;
            width: 200px;
            height: 200px;
            background-color: antiquewhite;
            text-decoration: none;
            list-style: none;
            text-decoration: none;
            color: black;
}

.mov__link:hover {
    cursor: pointer;
    background-color: bisque;
    text-decoration: underline;
}
.inputFilm{
    font-size: 20px;
}
.btn__find {
    background-color: blanchedalmond;
    width: 80px;
    height: 30px;
}
form {
    display: flex;
}